@include media-breakpoint-down(xl) {
    .query-builder {
        min-width: 700px;
    }
}

@include media-breakpoint-down(md) {
    .query-builder {
        min-width: 450px;
    }
}

@include media-breakpoint-down(xs) {
    .query-builder {
        min-width: auto;
    }
}

.pull-right {
    float:right;
}

.query-builder {
    .rules-group-container {
        width: 100%;
        border: #ededed;
        background: #ededed;

        .rules-group-header {
            .btn-group {
                .btn {
                    color: #575757;
                    background-color: #ffffff;
                    border: 1px solid #888888;
                    margin-right: 5px;
                    width: auto;
                }

                .btn:hover {
                    color: #575757;
                    background-color: #d7ebf8;
                    border-color: #00a0df;
                }

                .btn:active {
                    color: #575757;
                    background-color: #cccccc;
                    border-color: #888888;
                }
            }
        }
    }

    .rules-group-header {
        .btn-group.group-conditions {
            display: none;
        }
    }

    .rules-group-body {
        .rules-list {
            padding: 0;

            .rule-container {
                .rule-header {
                    .btn-group.pull-right.rule-actions {
                        .btn.btn-default.btn-sm:hover {
                            opacity: .75;
                        }

                        .btn.btn-default.btn-sm:focus {
                            box-shadow: unset;
                        }
                    }
                }

                .rule-value-container {
                    border: none;
                }

                .rule-filter-container {
                    .form-control {
                        height: 30px;
                        padding: 0;
                    }

                    select {
                        option {
                            border: solid 1px #cccccc;
                            background-color: #ffffff;

                            &:hover {
                                border: solid 1px #00a0df;
                                background-color: #d7ebf8;
                            }

                            &:active {
                                background-color: #ffffff;
                                border: solid 1px #00a0df;
                            }

                            &:checked {
                                color: #ffffff;
                                background-color: #00a0df;
                            }
                        }

                        &:hover {
                            border: solid 1px #00a0df;
                            background-color: #d7ebf8;
                        }

                        &:active {
                            background-color: #ffffff;
                            border: solid 1px #00a0df;
                        }
                    }
                }

                .rule-operator-container {
                    .form-control {
                        height: 30px;
                        padding: 0;
                    }

                    select {
                        display: none;
                    }
                }

                input[type="text"] {
                    width: 15rem;
                    height: 30px;
                    border: 1px solid #ced4da;
                    border-radius: .2rem;
                    outline: none;

                    &:focus {
                        border-color: #00a0df;
                    }
                }
            }

            > *:before, *:after {
                border-width: 0px;
            }
        }
    }
}












.query-builder .rules-group-body .rules-list .rule-container .bootstrap-tagsinput {
    display: block;
    padding-left: 2.8em;
}

.query-builder .rules-group-body .rules-list .rule-container .bootstrap-tagsinput input[type="text"] {
    border-style: none;
}

.query-builder .rules-group-body .rules-list .rule-container input[list]:hover,
.query-builder .rules-group-body .rules-list .rule-container datalist option:hover {
    border: solid 1px #00a0df;
    background-color: #d7ebf8;
}

.query-builder .error-container {
    visibility: hidden;
    display: inline-block !important;
}

.query-builder .has-error .error-container {
    visibility: visible;
}